<template>
  <div class="container">
    <div class="panel panel-default">
      <div class="panel-body" id="nodechart" style="height: 500px"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted () {
    this.drawLine()
  },
  methods: {
    drawLine () {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById('nodechart'))
      // 绘制图表
      var option = {
        title: {
          text: '生产线节点监控',
          x: 'center'
        },
        tooltip: {
          trigger: 'axis'
        },
        toolbox: {
          show: true,
          feature: {
            dataZoom: {
              yAxisIndex: 'none'
            },
            dataView: {readOnly: false},
            magicType: {type: ['line', 'bar']},
            restore: {},
            saveAsImage: {}
          }
        },
        xAxis: {
          data: ['go', 'java', 'adc', 'ap', 'ad', 'sql']
        },
        yAxis: {},
        series: [{
          name: '异常次数',
          type: 'line',
          markLine: {
            data: [
              {type: 'average', name: '平均值'}
            ]
          },
          // 显示数值
          // itemStyle: {
          //   normal: {label: {show: true}}
          // },
          lineStyle: {
            color: {
              type: 'linear',
              x: 0,
              y: 0,
              x2: 0,
              y2: 1,
              colorStops: [{
                offset: 0, color: 'red' // 0% 处的颜色
              }, {
                offset: 1, color: 'gray' // 100% 处的颜色
              }],
              globalCoord: false // 缺省为 false
            }
          },
          markPoint: {
            data: [
              {type: 'max', name: '最大值'},
              {type: 'min', name: '最小值'}
            ]
          },
          data: [5, 20, 36, 10, 10, 20]
        }]
      }
      myChart.setOption(option)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



