import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import NodeGraph from '@/components/NodeGraph'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'NodeGraph',
      component: NodeGraph
    }
  ]
})



